body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
}

/* Page Styles */
.page {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f4f4f4;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  color: #333;
}

.property-list {
  /* Add your styling for property list */
}

.whatsapp-icon {
  position: fixed;
  bottom: 60px;
  right: 60px;
  background-color: #25d366;
  color: #fff;
  padding: 10px;
  width: 100px;
  border-radius: 15%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s;
}

.whatsapp-icon:hover {
  background-color: #128c7e;
}

/* Home Page Styles */
.home {
  max-width: 100vw;
  margin: 20px auto;
  padding: 20px;
  background-color: #f4f4f4;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  color: #333;
  text-align: center;
}

.property-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 20px;
}

.property-card {
  width: calc(32% - 20px);
  margin-bottom: 20px;
  padding: 10px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.property-card:hover {
  transform: scale(1.05);
}

.property-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
}

.property-card h2 {
  margin-top: 10px;
  font-size: 18px;
}

.property-card p {
  color: #555;
  margin-top: 5px;
  font-size: 14px;
}

.price {
  color: #27ae60;
  font-weight: bold;
  margin-top: 10px;
}


.whatsapp-icon:hover {
  background-color: #14514a;
}

.additional-info {
  display: flex;
  justify-content: space-around;
  margin: 10px 0;
}

.info-item {
  text-align: center;
  width: 25vw;
  padding: 20px;
  background-color: #ececec;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(253, 253, 253, 0.1);
  transition: transform 0.3s;
}

.info-item:hover {
  transform: scale(1.05);
}

.info-item h2 {
  font-size: 18px;
  color: #000;
}

.info-item p {
  color: #0f5e4d;
  margin-top: 5px;
  font-size: 20px;
  font-weight: bolder;
}

.homeSlider {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 20vw;
}

.homeSliderImg {
  display: flex;
  object-fit: fill;
  width: 100vw;
  height: 30vw;
}